
.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}
.logo{
    position: fixed;
    left: 20px;
    width: 80px;
}
.avatar{
    position:fixed;
    right: 20px;
    width: 30px;
}
.nav-items{
    color: white;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    margin-left: 15px;

}